import React from 'react';
import styled from 'styled-components';

import { servicecontent } from './services-content';

const Service = styled.div`
    padding: 20px;
    text-align:center;
    width: 750px;
    line-height:1.8em;
    margin: auto;
    
    p {
        text-align: left;
        font-size: 18px;
        animation: text 3s 1;
    }

    @media (max-width:768px) {
        width: 90%;
        margin-left:auto;
        margin-right:auto;
    }

    @media (min-width:768px) and (max-width:1200px) {
        width: 600px;
        margin-left:auto;
        margin-right:auto;
    }


    @keyframes text {
        0%{
            color:#f1faee;
            margin-left: -20px;
        }
    }

`;



const DigitalTransformation = () => {
    
    const digitalMarketing = servicecontent.filter(servicecontent => servicecontent.name.toLowerCase().includes("digital-transformation"))[0];

    return (
        <Service>

            <h2>{digitalMarketing.title}</h2>

            {digitalMarketing.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}

        </Service>
    )
};

export default DigitalTransformation;