import React from 'react';
import { ReactComponent as Logo } from './sfm-logo.svg';

import styled from 'styled-components';
import { Link } from 'react-router-dom';


const SfmLogo = styled(Logo)`
    width: 300px;
    @media (max-width: 1200px) {
        width: 300px;
    }
    @media (max-width: 768px) {
        width: 200px;
    }
`;

const StyledBurger = styled.button`
  position: absolute;
  top: 30px;
  right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
  &:focus {
    outline: none;
  }
  div {
    width: 2rem;
    height: 0.25rem;
    background: ${({ open }) => open ? '#f1faee' : '#1d3557'};
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;
    :first-child {
      transform: ${({ open }) => open ? 'rotate(45deg)' : 'rotate(0)'};
    }
    :nth-child(2) {
      opacity: ${({ open }) => open ? '0' : '1'};
      transform: ${({ open }) => open ? 'translateX(20px)' : 'translateX(0)'};
    }
    :nth-child(3) {
      transform: ${({ open }) => open ? 'rotate(-45deg)' : 'rotate(0)'};
    }
  }
`

const OverlayMenu = styled.div`
    height: ${({ open }) => open ? '100%': '0%'};
    width: 100%;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden; 
    transition: 0.5s; 
`;

const OverlayContent = styled.div`
    position: relative;
    top: 25%; 
    width: 100%; 
    text-align: center; 
    margin-top: 30px;
    
`;

const StyledLink = styled(Link)`

    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #f1faee;
    display: inline;
    margin: 20px; 
    transition: 0.3s;

    &:hover, &:focus {
        color: #a8dadc;
    }

    @media (max-width:768px) {
        display:block;
        margin:0;
        font-size: 20px;
    }
`;


const Nav = () => {
    const [open, setOpen] = React.useState(false);

    const refreshPage = () => {
        window.location.reload(false);
      };

    return (
        <>
        <nav>
            <a href="/" onClick={refreshPage}><SfmLogo /></a>
            <StyledBurger open={open} onClick={() => setOpen(!open)}>
                 <div />
                 <div />
                 <div />
             </StyledBurger>
        </nav>
        
        <OverlayMenu open={open}>
            <OverlayContent>
                <StyledLink onClick={() => setOpen(!open)} to="/" ><span>Home</span></StyledLink>
                <StyledLink onClick={() => setOpen(!open)} to="/services"><span>Services</span></StyledLink>
                <StyledLink onClick={() => setOpen(!open)} to="/work"><span>Work</span></StyledLink>
                <StyledLink onClick={() => setOpen(!open)} to="/about"><span>About</span></StyledLink>
                {/* <StyledLink onClick={() => setOpen(!open)} to="/blog"><span>Blog</span></StyledLink> */}
                <hr className="menubar"></hr>
                <StyledLink onClick={() => setOpen(!open)} style={{fontSize:"18px"}} to="/contact">Contact</StyledLink>
                <div className="vertical-bar"></div>
                <StyledLink onClick={() => setOpen(!open)} style={{fontSize:"18px"}} to="/subscribe">Subscribe</StyledLink>
            </OverlayContent>
        </OverlayMenu>
        </>
    )
};

export default Nav;