import React from 'react';
import styled, { keyframes } from 'styled-components';

import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { NavLink, useLocation } from 'react-router-dom';

import DigitalTransformation from './services/Digitaltransformation';
import SalesInnovation from './services/Salesinnovation';
import ContentMarketing from './services/Contentmarketing';
import Ecommerce from './services/Ecommerce';

const SlideLink = styled(NavLink)`
    color:#a8dadc;
    text-decoration:none;
    display:inline;
    margin: 40px;
    font-size: 26px;
    border-bottom-width: 2px;

    &:hover {
        color:#1d3557;
    }

    @media (max-width:768px){
        // font-size:15px;
        // margin:10px;
        display:none;
    }

    @media (min-width:768px) and (max-width:1200px) {
        margin:20px;
        font-size:18px;
    }
`;

const SliderMenu = styled.div`
    position:relative;
    text-align:center;
`;

const Drop = styled.div`
    display:none;
    justify-content: center;
    align-content: center;
    align-items: center;

    @media (max-width:768px) {
        display:block;
    }
`;

const DropList = styled.ul`
    width:50%;
    margin:auto;
    font-size:20px;
    background-color:#f1faee;
    padding: 5px 10px;
`;

const SlideDown = keyframes`
    from {
        opacity: 0;
        transform: translateY(-10%);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
`;

const DropDown = styled.div`
    list-style:none;
    position:absolute;
    background-color:#f1faee;
    display: ${({ down }) => down ? "inline-block" : "none"};
    animation: ${SlideDown} 1s 1;
`;

const DropListItem = styled.li`
    padding: 12px;
    color: #1d3557;
`;

const Header = styled.li`
    list-style:none;
    margin-bottom: 5px;
    padding-bottom: 5px;
    border-bottom: 2px solid #1d3557;
    color: #1d3557;

    &:hover {
        cursor: pointer
    }
`;

const Arrow = styled.i`
    border: solid #1d3557;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 4px;
    margin-left: 15px;
    margin-bottom: 3px;


    transform: ${({ down }) => down ? "rotate(-135deg)" : "rotate(45deg)"} ;
    -webkit-transform: ${({ down }) => down ? "rotate(-135deg)" : "rotate(45deg)"};
`;

const SliderNav = () => {
    return(
    <SliderMenu>
    
        <SlideLink activeStyle={{color:"#1d3557"}} to="/services/digital"><span>Digital Transformation</span></SlideLink>
        <SlideLink activeStyle={{color:"#1d3557"}} to="/services/innovation"><span>Sales Innovation</span></SlideLink>
        <SlideLink activeStyle={{color:"#1d3557"}} to="/services/content"><span>Content Marketing</span></SlideLink>
        <SlideLink activeStyle={{color:"#1d3557"}} to="/services/ecommerce"><span>E-commerce</span></SlideLink>
    
    </SliderMenu>
    )
};

const Dropdown = () => {
    const [down, setDown] = React.useState(false);

    const dropdown_dict = {
        "/services/innovation":"Sales Innovation",
        "/services/digital":"Digital Transformation",
        "/services/content":"Content Marketing",
        "/services/ecommerce":"E-commerce"
    };
    let location = useLocation();

    var arr = [];

    for(var key in dropdown_dict) {
        var value = dropdown_dict[key];
        if (value !== dropdown_dict[location.pathname]) {
                arr.push(key);
            }
      }

    return(
        <Drop>
            <DropList>
                <Header onClick={() => setDown(!down)}>{dropdown_dict[location.pathname]}<Arrow down={down} /> </Header>
                <DropDown down={down}>
                    {arr.map((item,key) => <DropListItem key={key}><a style={{textDecoration:"none", color:"#1d3557"}} href={item}>{dropdown_dict[item]}</a></DropListItem>)}
                </DropDown>
            </DropList>
        </Drop>
    )
};

const Services = () => {

    return (
        <Router>
            <div className="slider">
                <SliderNav />
                <Dropdown />
                    <Switch>
                        <Route exact path="/services"><Redirect to="/services/digital" /></Route>
                        <Route path="/services/digital" component={DigitalTransformation}/>
                        <Route path="/services/innovation" component={SalesInnovation} />
                        <Route path="/services/content" component={ContentMarketing} />
                        <Route path="/services/ecommerce" component={Ecommerce} />
                    </Switch>
            </div>
        </Router>
    )
};

export default Services;